    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    border: none;
    outline: none;
    scroll-behavior: smooth;
    font-family: -apple-system, sans-serif;
}

.animated-section {
    background-color: var(--bg-color);
    margin: 0;
    opacity: 0; /* Initially hidden */
    transform: translateY(60px); /* Slightly move down */
    transition: all 0.7s ease-out;
  }
.animated-section.visible {
    opacity: 1;
    transform: translateY(0);
}
:root {
    --bg-color:#353333;
    --second-bg-color:#111;
    --text-color:#fff;
    --main-color:#17db48;
}

html {
    font-size: 62.5%;
    overflow-x: hidden;
}
body {
    background: var(--bg-color);
    color: var(--text-color);
}

section {
    min-height: 100vh;
    padding: 10rem 8% 2rem;
}
.footer_section {
    min-height: 0;
    padding: 0 0;
    margin: 0 auto;
} 
/* Header section */

header {
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 9%;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    transition: all 0.5s ease-in-out;    
}

.menu_phone {
    display: none;
    cursor: pointer;
    font-size: 2.8em;
}

.logo {
    font-size: 3rem;
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}
.logo:hover {
    transform: scale(1.03);
}

span {
    background: linear-gradient(to right, #ff204e, #ff008e);
    background-clip: text;
    color: transparent;
}

.navbar {
    display: flex;
}
.navbar a {
    font-size: 1.8rem;
    font-weight: 500;
    color: var(--text-color);
    margin-left: 4rem;
    text-decoration: none;
    border-bottom: 3px solid transparent;
}

.navbar a:hover,
.navbar a.active {
    color: var(--main-color);
    border-bottom: 3px solid var(--main-color);
}

#menu_icon {
    font-size: 3.5rem;
    color: var(--main-color);
    cursor: pointer;
    display: none;

}

.nav_btn {
    display: inline-block;
    padding: 1rem 2.8rem ;
    background-color: transparent;
    color: var(--main-color);
    border: 2px solid var(--main-color);
    border-radius: 1rem;
    font-size: 1.6rem;
    font-weight: 600;
    transition: all 0.3s ease-in-out;
    text-decoration: none;

}

.nav_btn i {
    vertical-align: middle;
    font-size: 1.6rem;
}
.nav_btn :hover {
    background: var(--main-color);
    color: var(--text-color);
    box-shadow: 0 0 20px;
}

/* for phone */

.dropdown-menu {
    display: none;
    position: absolute;
    top: 70px;
    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter:blur(10px);
    border-radius:5px;
}
.dropdown-menu li .dropdown-item-text {
    color:#17db48;
    font-size: 1.8em;
}
.dropdown-menu li a {
    color: white;
    font-size: 1.6em;
    text-decoration: underline;
}

.btn i {
    position: relative;
    top: 3px;
}

/*Home section*/

section {
    min-height: 100vh;
    padding: 10rem 8% 10rem;
}
.home {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}
.home_content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 3rem;
    text-align: center;
}

.refill-title, span {
    background: linear-gradient(to right, #93AAA1, #3A5C5E);
    background-clip: text;
    color: transparent;
}

