Y;IAChB;IACA;QACI,gBAAgB;IACpB;AACJ;;AAEA;IACI;QACI,kBAAkB;QAClB,UAAU;QACV,UAAU;QACV,mBAAmB;IACvB;AACJ","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    text-decoration: none;\r\n    list-style: none;\r\n    border: none;\r\n    outline: none;\r\n    scroll-behavior: smooth;\r\n    font-family: -apple-system, sans-serif;\r\n}\r\n\r\n.animated-section {\r\n    background-color: var(--bg-color);\r\n    margin: 0;\r\n    opacity: 0; /* Initially hidden */\r\n    transform: translateY(60px); /* Slightly move down */\r\n    transition: all 0.7s ease-out;\r\n  }\r\n.animated-section.visible {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n}\r\n:root {\r\n    --bg-color:#353333;\r\n    --second-bg-color:#111;\r\n    --text-color:#fff;\r\n    --main-color:#17db48;\r\n}\r\n\r\nhtml {\r\n    font-size: 62.5%;\r\n    overflow-x: hidden;\r\n}\r\nbody {\r\n    background: var(--bg-color);\r\n    color: var(--text-color);\r\n}\r\n\r\nsection {\r\n    min-height: 100vh;\r\n    padding: 10rem 8% 2rem;\r\n}\r\n.footer_section {\r\n    min-height: 0;\r\n    padding: 0 0;\r\n    margin: 0 auto;\r\n} \r\n/* Header section */\r\n\r\nheader {\r\n    position: fixed;\r\n    width: 100%;\r\n    top: 0;\r\n    right: 0;\r\n    z-index: 1000;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 2rem 9%;\r\n    background: rgba(0, 0, 0, 0.3);\r\n    backdrop-filter: blur(10px);\r\n    transition: all 0.5s ease-in-out;    \r\n}\r\n\r\n.menu_phone {\r\n    display: none;\r\n    cursor: pointer;\r\n    font-size: 2.8em;\r\n}\r\n\r\n.logo {\r\n    font-size: 3rem;\r\n    font-weight: 800;\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n    transition: 0.3s ease-in-out;\r\n}\r\n.logo:hover {\r\n    transform: scale(1.03);\r\n}\r\n\r\nspan {\r\n    background: linear-gradient(to right, #ff204e, #ff008e);\r\n    background-clip: text;\r\n    color: transparent;\r\n}\r\n\r\n.navbar {\r\n    display: flex;\r\n}\r\n.navbar a {\r\n    font-size: 1.8rem;\r\n    font-weight: 500;\r\n    color: var(--text-color);\r\n    margin-left: 4rem;\r\n    text-decoration: none;\r\n    border-bottom: 3px solid transparent;\r\n}\r\n\r\n.navbar a:hover,\r\n.navbar a.active {\r\n    color: var(--main-color);\r\n    border-bottom: 3px solid var(--main-color);\r\n}\r\n\r\n#menu_icon {\r\n    font-size: 3.5rem;\r\n    color: var(--main-color);\r\n    cursor: pointer;\r\n    display: none;\r\n\r\n}\r\n\r\n.nav_btn {\r\n    display: inline-block;\r\n    padding: 1rem 2.8rem ;\r\n    background-color: transparent;\r\n    color: var(--main-color);\r\n    border: 2px solid var(--main-color);\r\n    border-radius: 1rem;\r\n    font-size: 1.6rem;\r\n    font-weight: 600;\r\n    transition: all 0.3s ease-in-out;\r\n    text-decoration: none;\r\n\r\n}\r\n\r\n.nav_btn i {\r\n    vertical-align: middle;\r\n    font-size: 1.6rem;\r\n}\r\n.nav_btn :hover {\r\n    background: var(--main-color);\r\n    color: var(--text-color);\r\n    box-shadow: 0 0 20px;\r\n}\r\n\r\n/* for phone */\r\n\r\n.dropdown-menu {\r\n    display: none;\r\n    position: absolute;\r\n    top: 70px;\r\n    background-color: rgba(0, 0, 0, 0.3);\r\n    backdrop-filter:blur(10px);\r\n    border-radius:5px;\r\n}\r\n.dropdown-menu li .dropdown-item-text {\r\n    color:#17db48;\r\n    font-size: 1.8em;\r\n}\r\n.dropdown-menu li a {\r\n    color: white;\r\n    font-size: 1.6em;\r\n    text-decoration: underline;\r\n}\r\n\r\n.btn i {\r\n    position: relative;\r\n    top: 3px;\r\n}\r\n\r\n/*Home section*/\r\n\r\nsection {\r\n    min-height: 100vh;\r\n    padding: 10rem 8% 10rem;\r\n}\r\n.home {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    flex-direction: column;\r\n    gap: 1rem;\r\n}\r\n.home_content {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    flex-direction: column;\r\n    margin-top: 3rem;\r\n    text-align: center;\r\n}\r\n