r\n    box-shadow: 0 0 22px var(--main-color);\r\n}\r\n/*ScrollBar Design*/\r\n\r\n::-webkit-scrollbar {\r\n    width: 15px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n    background: linear-gradient(to bottom, #6b6365, #353033);\r\n    width: 50px;\r\n}\r\n\r\n/*Skills section*/\r\n\r\n.skills {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 10rem;\r\n    background: var(--second-bg-color);\r\n    color: var(--text-color);\r\n}\r\n.skills_pic {\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: 50%;\r\n}\r\n.skill_img {\r\n    width: 90%;\r\n}\r\n.skill_img img {\r\n    position: relative;\r\n    width: 40vw;\r\n    height: 20vw;\r\n    object-fit: cover;\r\n    border-radius: 3%;\r\n    box-shadow: 0 0 10px var(--main-color);\r\n    top: 1rem;\r\n}\r\n\r\n.skill_content {\r\n    position: relative;\r\n    left:-15px;\r\n    width: 100%;\r\n    background:transparent;\r\n    margin: 0 0;\r\n    padding: 10px 20px;\r\n    border-radius: 7px;\r\n}\r\n\r\n.skill_content .skill_box {\r\n    width: 40vw;\r\n    margin-bottom: 30px;\r\n}\r\n\r\n.skill_box .title {\r\n    display: block;\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    color: var(--text-color);\r\n}\r\n\r\n.skill_box .skill_bar {\r\n    height: 8px;\r\n    width: 100%;\r\n    border-radius: 6px;\r\n    margin-top: 6px;\r\n    background: var(--bg-color);\r\n}\r\n\r\n.skill_bar .skill_per {\r\n    position: relative;\r\n    display: block;\r\n    height: 100%;\r\n    width: 90%;\r\n    border-radius: 6px;\r\n    background: var(--main-color);\r\n    animation: progress 0.4s ease-in-out forwards;\r\n    opacity: 0;\r\n}\r\n\r\n.skill_per.html {\r\n    width: 90%;\r\n    animation-delay:0.1s\r\n}\r\n\r\n.skill_per.css {\r\n    width: 80%;\r\n    animation-delay: 0.2s;\r\n}\r\n.skill_per.js {\r\n    width: 68%;\r\n    animation-delay: 0.3s;\r\n}\r\n\r\n.skill_per.react {\r\n    width: 30%;\r\n    animation-delay: 0.4s;\r\n}\r\n\r\n@keyframe